.crumbs {
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  background-color: #f5f5f5;
  list-style: none;
  border-radius: 0.25rem;
}

.crumbs::after {
  content: "";
  display: block;
  clear: both;
}

.crumbs-item {
  float: left;
}

.crumbs-item + .crumbs-item::before {
  content: "/";
  display: inline-block;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  color: #f5f5f5;
}

.crumbs-item + .crumbs-item:hover::before {
  text-decoration: underline;
}

.crumbs-item + .crumbs-item:hover::before {
  text-decoration: none;
}

.crumbs-item.active {
  color: #f5f5f5;
}

ul.menu, ol.menu {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.menu-title {
  display: block;
  padding: .3rem .5rem;
  font-weight: 400;
  color: #212121;
  text-decoration: none;
}

.menu-title:hover:focus, .menu-title:hover:hover, .menu-title:focus:focus, .menu-title:focus:hover {
  color: #2196f3;
  text-decoration: none;
}

.menu-title.active {
  color: #2196f3;
}

.menu-header {
  font-size: 1rem;
  font-weight: 700;
  color: #212121;
}

.menu-dropdown::after {
  content: "";
  display: block;
  clear: both;
}

.menu-dropdown.menu .menu {
  display: none;
}

.menu-dropdown > .menu-item, .menu-dropdown > li {
  float: left;
}

.menu-dropdown > .menu-item:hover > .menu, .menu-dropdown > li:hover > .menu {
  display: block;
}

.menu-cascaded {
  position: relative;
}

.menu-cascaded > .menu {
  position: absolute;
  top: 0;
  left: 90%;
  width: 100%;
}

.menu-cascaded:hover > .menu {
  display: block;
}

.menu-catalogue {
  counter-reset: catalogue1;
}

.menu-catalogue > .menu-item > a::before, .menu-catalogue > li > a::before {
  content: counter(catalogue1, decimal) ". ";
  margin-right: .5rem;
  font-weight: bold;
  counter-increment: catalogue1;
}

.menu-catalogue .menu-catalogue {
  counter-reset: catalogue2;
}

.menu-catalogue .menu-catalogue > .menu-item > a::before, .menu-catalogue .menu-catalogue > li > a::before {
  content: counter(catalogue1, decimal) "." counter(catalogue2, decimal) ". ";
  counter-increment: catalogue2;
}

.menu-catalogue .menu-catalogue .menu-catalogue {
  counter-reset: catalogue3;
}

.menu-catalogue .menu-catalogue .menu-catalogue > .menu-item > a::before, .menu-catalogue .menu-catalogue .menu-catalogue > li > a::before {
  content: counter(catalogue1, decimal) "." counter(catalogue2, decimal) "." counter(catalogue3, decimal) ". ";
  counter-increment: catalogue3;
}

.menu-catalogue .menu-catalogue .menu-catalogue .menu-catalogue {
  counter-reset: catalogue4;
}

.menu-catalogue .menu-catalogue .menu-catalogue .menu-catalogue > .menu-item > a::before, .menu-catalogue .menu-catalogue .menu-catalogue .menu-catalogue > li > a::before {
  content: counter(catalogue1, decimal) "." counter(catalogue2, decimal) "." counter(catalogue3, decimal) "." counter(catalogue4, decimal) ". ";
